.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  width: 300px;
  margin: auto;
  box-shadow: 0px 0px 12px var(--border);
  border-radius: 8px;
  background-color: var(--form-bg);
}

.top {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.top i {
  font-size: 1rem;
}

.divider {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid var(--text-primary);
  line-height: 0.1rem;
  margin: 2rem 0;
}

span {
  padding: 0 10px;
  background-color: var(--form-bg);
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

label {
  margin: 8px 0;
  font-weight: 600;
}

